.wrapper {
    width: 100%;
    max-width: 1390px;
    padding: 0 20px;
    margin: 0 auto;
  }
.wrapper-s {
  max-width: 1170px;
}
.wrapper-m {
  max-width: 1270px;
}
.wrapper-l {
  max-width: 1370px;
}
.wrapper-xl {
  max-width: 1520px;
}
.title {
  padding-left: 175px;
  font-family: "Circe-ExtraLight";
  font-size: 52px;
  line-height: 52px;
  text-transform: uppercase;
  color: #06828a;
  @media (max-width: 1250px) {
    padding-left: 0;
    font-size: 39px;
    line-height: 39px;
  }
}
.form-field {
  width: 100%;
  max-width: 208px;
  height: 35px;
  padding: 0 13px;
  background-color: none;
  font-family: "Century Gothic Regular";
  font-size: 16px;
  line-height: 16px;
  color: #939393;
  background: none;
  border: 1px solid #c2c0b3;
  transition: 0.3s ease;
  
  @media (max-width: 1100px) {
    max-width: 170px;
  }
  &.error {
    border: 1px solid red;
  }
  &:focus {
    border: 1px solid #ffffff;
  }
}
.form-btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 220px;
  height: 35px;
  padding: 0 10px 0 23px;
  background: none;
  font-family: "Circe-ExtraLight";
  font-size: 16px;
  line-height: 35px;
  text-transform: uppercase;
  color: #939393;
  border: 1px solid #c2c0b3;
  transition: 0.3s ease;
  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 0.5px);
    right: 33px;
    display: block;
    width: 30px;
    height: 1px;
    background-color: #c2c0b3;
    transition: 0.3s ease;
  }
  &:hover {
    border: 1px solid #ffffff;
    color: #ffffff;
  }
  &:hover::before {
    background-color: #ffffff;
  }
}